/* Custom gutenberg wrapper */
.wp-block-columns{
    &.alignfull{
        max-width: 90vw !important; // equal with theme.json

        @media screen and (max-width: 1024px) {
            max-width: 100vw !important;
            padding-left: 20px;
            padding-right: 20px;
        }
    }
    &.alignwide{
        max-width: 1170px !important;
    }
}