/* ======================================================================== */
/* 07. Component - Site Footer */
/* ======================================================================== */
.site-footer{
    ul{
        list-style: none outside none;
        margin: 0;
        padding: 0;
    }

    a{
        text-decoration: underline;
        &:hover,
        &:focus{
            color: var(--wp--preset--color--black) !important;
            text-decoration: none;
        }
    }
}