/* ======================================================================== */
/* 05. Helper - Space */
/* ======================================================================== */
.has-space{
    &-top{
        &-default{}
        &-small{
            padding-top: 1.6rem;
        }
        &-medium{
            padding-top: 5rem;
        }
        &-large{}
    }
    &-bottom{
        &-default{}
        &-small{
            padding-bottom: 1.6rem;
        }
        &-medium{
            padding-bottom: 5rem;
        }
        &-large{}
    }
}