/* ======================================================================== */
/* 06. Component - Site Header */
/* ======================================================================== */
.site-header{
    .wp-block{

        &-navigation{
            &-item{
                &__content{
                    font-weight: bold;
                    &:hover,
                    &:focus{
                        opacity: .65;
                    }
                }
            }         

            &__responsive{
                &-container{
                    @media screen and (max-width: 1024px){
                        align-items: flex-start;
                        bottom: 0;
                        display: none;
                        justify-content: flex-start;
                        left: 0;
                        position: fixed;
                        right: 0;
                        top: 0;
                        z-index: 1;
                    }

                    // Menu dialog opened
                    &.is-menu-open{
                        background-color: inherit;
                        display: flex;
                        flex-direction: column;
                        overflow: auto;
                        padding: 24px;
                        z-index: 100000;
                    }
                    
                    
                    // Hamburger button - open
                    &-open{
                        @media screen and (max-width: 1024px) {
                            display: block;   
                        }
                    }
                    // Hamburger button - close
                    &-close{}
                }
            }
        }
    }

    .wp-block-column{
        &:first-child{
            @media screen and (max-width: 599px) {
                flex-basis: 80% !important;
            }
        }
        &:last-child{
            @media screen and (max-width: 599px) {
                flex-basis: 20% !important;
            }
        }
    }
}