/* ======================================================================== */
/* 08. Component - Site Article */
/* ======================================================================== */
.site-article{
    &__featured-image{
        &.alignfull{
            max-width: 100% !important;
        }
    }

    &__title{
        margin-bottom: 1.6rem;
    }

    &__meta{
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        align-items: center;
        > div {
            margin-left: .35rem;
            padding-left: .35rem;
            margin-right: .35rem;
            padding-right: .35rem;
            position: relative;

            &:after{
                content: "/";
                position: absolute;
                right: -8px;
                color: var(--wp--preset--color--gray-second) !important;
            }

            &.site-article__meta{
                &-date{
                    padding-left: 0;
                    margin-left: 0;
                }
                &-tags{
                    padding-right: 0;
                    margin-right: 0;

                    &:after{
                        display: none;
                    }
                }
                &-author{
                    &:after{
                        top: -4px;
                    }
                }
            }

            a{
                &:hover,
                &:focus{
                    color: var(--wp--preset--color--gray-second) !important;
                }
            }
        }

        .wp-block-post-author__name{
            font-size: var(--wp--preset--font-size--text-smallest) !important;
            font-weight: normal;
            color: var(--wp--preset--color--gray-dark) !important;
        }
    }

    &__post{
        font-size: 1rem;
        letter-spacing: 0;

        a{
            font-weight: bold;
            text-decoration: underline;
            &:hover,
            &:focus{
                text-decoration: none;
            }
        }
    }
}