// Default Form
form{
    p + p{
        margin-top: 1rem;
    }
    label{
        .required{
            color: red;
        }
    }

    input[type="text"],
    input[type="email"],
    input[type="url"],
    textarea{
        width: 100%;
        padding: 1rem;
        border-radius: 12px;
        font-size: 1rem;
        &:focus,
        &:active{
            outline: none;
        }
    }
}


// Comment Form
.comment-form{
    p{
        label{
            font-size: var(--wp--preset--font-size--text-smallest);
            font-weight: bold;
        }
    }

    .form-submit{
        margin-top: 1rem;
    }
}